
import { Options, Vue } from "vue-class-component";
import { mapState } from "vuex";

@Options({
  props: {
    msg: String
  },

  data() {
    return {
      chosenArticle: ""
    };
  },

  //get api array
  computed: {
    ...mapState(["news"])
  },

  created() {
    //get api array
    this.$store.dispatch("loadNews");
    //start random article on pageload
    this.picker();
  },

  //random article button logic
  methods: {
    picker() {
      const chosenNumber = Math.floor(Math.random() * this.news.length);
      this.chosenArticle = this.news[chosenNumber];
      console.log(this.chosenArticle);
    }
  }
})
export default class Random extends Vue {}
