<template>
  <!-- article section -->
  <div class="container" id="container-random">
    <div class="article">
      <p class="title">{{ chosenArticle.title }}</p>
      <p class="description">{{ chosenArticle.description }}</p>
      <hr />
      <img v-bind:src="chosenArticle.urlToImage" alt="News article image" />
      <p class="author">{{ chosenArticle.author }}</p>
    </div>
    <!-- button section -->
    <button v-on:click="picker" id="random-button" class="button">
      Give me another one
    </button>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from "vue-class-component";
import { mapState } from "vuex";

@Options({
  props: {
    msg: String
  },

  data() {
    return {
      chosenArticle: ""
    };
  },

  //get api array
  computed: {
    ...mapState(["news"])
  },

  created() {
    //get api array
    this.$store.dispatch("loadNews");
    //start random article on pageload
    this.picker();
  },

  //random article button logic
  methods: {
    picker() {
      const chosenNumber = Math.floor(Math.random() * this.news.length);
      this.chosenArticle = this.news[chosenNumber];
      console.log(this.chosenArticle);
    }
  }
})
export default class Random extends Vue {}
</script>
<style scoped lang="scss">
#container-random {
  display: grid;
}
</style>
